import React, { Component } from 'react';
import newId from './utils/NewId';
import {
  Table,
  TableBody,
  TableHeader,
  TableHeaderColumn,
  TableRow,
  TableRowColumn,
} from 'material-ui/Table';

import {Card, CardText} from 'material-ui/Card';
import Avatar from 'material-ui/Avatar';

class Crypto extends Component {

  constructor(props) {
    super(props);
    this.state = {
      items: props.items,
      showCheckboxes: false,
      selectable: false
    }
  }


  render() {
    if (this.props.items){
      return (
        <div>
          <Card style={{marginBottom: 15}}>
            <CardText expandable={false}>
              TOTAL EARNINGS: $ {this.props.total}
            </CardText>
          </Card>
          <Table>
            <TableHeader adjustForCheckbox={false} displaySelectAll={false}>
              <TableRow>
                <TableHeaderColumn>ICON</TableHeaderColumn>
                <TableHeaderColumn>ID</TableHeaderColumn>
                <TableHeaderColumn>NB Coins</TableHeaderColumn>
                <TableHeaderColumn>$ / Coin</TableHeaderColumn>
                <TableHeaderColumn>$$$</TableHeaderColumn>
              </TableRow>
            </TableHeader>
            <TableBody displayRowCheckbox={false}>
              {this.props.items.map(item =>
                <TableRow key={newId()} selectable={false}>
                  <TableRowColumn>
                    <Avatar
                      src={item.icon}
                      size={50}
                      style={{margin: 5}}
                    />
                  </TableRowColumn>
                  <TableRowColumn>{item.symbol} - {item.coin}</TableRowColumn>
                  <TableRowColumn>{item.total_unit}</TableRowColumn>
                  <TableRowColumn>$ {item.price}</TableRowColumn>
                  <TableRowColumn>$ {item.value}</TableRowColumn>
                </TableRow>
              )}
            </TableBody>
          </Table>
        </div>
      );
    }
    return (
      <Table>
        <TableHeader>
          <TableRow>
            <TableHeaderColumn>ICON</TableHeaderColumn>
            <TableHeaderColumn>ID</TableHeaderColumn>
            <TableHeaderColumn>NB Coins</TableHeaderColumn>
            <TableHeaderColumn>$$$</TableHeaderColumn>
          </TableRow>
        </TableHeader>
      </Table>

    );
  }
}

export default Crypto;



