import React, { Component } from 'react';
import Crypto from './Crypto';
import config from './config';
// import logo from './logo.svg';
import RaisedButton from 'material-ui/RaisedButton';
import FlatButton from 'material-ui/FlatButton';
import TextField from 'material-ui/TextField';
import Paper from 'material-ui/Paper';
import Dialog from 'material-ui/Dialog';

import './App.css';

const stylePaper = {
  height: 300,
  width: 600,
  paddingTop: 55,
  margin: "0 auto",
  textAlign: 'center'
};
const styleText = {
  width: "90%",
  marginBottom: 30
}

const symbols = {
  "adzcoin": "ADZ",
  "auroracoin": "AUR",
  "bitcoin": "BTC",
  "bitcoin-cash": "BCH",
  "bitcoin-gold": "BTG",
  "dash": "DSH",
  "digibyte": "DGB",
  "digibyte-groestl": "DGB",
  "digibyte-skein": "DGB",
  "digibyte-qubit": "DGB",
  "ethereum": "ETH",
  "ethereum-classic": "ETC",
  "expanse": "EXP",
  "feathercoin": "FTC",
  "gamecredits": "GAME",
  "geocoin": "GEO",
  "globalboosty": "BSTY",
  "groestlcoin": "GRS",
  "litecoin": "LTC",
  "maxcoin": "MAX",
  "monacoin": "MONA",
  "monero": "XMR",
  "musicoin": "MUSIC",
  "myriadcoin": "XMY",
  "myriadcoin-skein": "XMY",
  "myriadcoin-groestl": "XMY",
  "myriadcoin-yescrypt": "XMY",
  "sexcoin": "SXC",
  "siacoin": "SC",
  "startcoin": "START",
  "verge": "XVG",
  "vertcoin": "VTC",
  "zcash": "ZEC",
  "zclassic": "ZCL",
  "zcoin": "XZC",
  "zencash": "ZEN"
}


class App extends Component {

  constructor(props) {
    super(props);
    this.onSubmit = this.onSubmit.bind(this);
    this.handleLogoutClick = this.handleLogoutClick.bind(this);
    this.handleClose = this.handleClose.bind(this);
    this.state = {
      datas: null,
      curr: null,
      isLoggedIn: props.isLoggedIn,
      total: 0,
      baseUrlImage: "https://www.cryptocompare.com/",
      openErrors: false,
      currentTime: null,
      lastUpdate: null
    }
  }


  handleClose() {
    this.setState({openErrors: false});
  };

  onSubmit(event) {
    event.preventDefault();

    let val = this.apiKey.input.value;

    if (val === '') {
      return;
    }

    this.getBalances(val)
  }

  getBalances(key){
    fetch(config.api.url + '&action=getuserallbalances&api_key=' + key, {
      mode: 'cors',
      headers: {
        'Access-Control-Allow-Origin': 'http://localhost:3000'
      }
    })
    .then(response => {
      if (!response.ok) {
        return Promise.reject(response.statusText);
      }
      return response.json();
    })
    .then(result => this.getCurr(result, key))
    .then(result => this.getPrice(result))
    .then(result => this.getIcons(result))
    .catch(error => {
      this.setState({openErrors: true});
    });
  }

  componentDidMount() {
    if (this.state.isLoggedIn) {
      this.getBalances(sessionStorage.getItem('apiKey'))
      this.setState({
        lastUpdate : new Date().toLocaleString()
      });
    }
    setInterval( () => {
      if (this.state.isLoggedIn) {
        this.setState({
          lastUpdate : new Date().toLocaleString()
        });
        this.getBalances(sessionStorage.getItem('apiKey'))
      }
    // }, 5000);
    }, 600000);
    setInterval( () => {
      this.setState({
        currentTime : new Date().toLocaleString()
      })
    }, 1000);
  }

  getPrice(result){
    var header = new Headers({
      'Access-Control-Allow-Origin':'*',
      'Content-Type': 'multipart/form-data'
    });
    return fetch(config.api.urlPrice + result, {
      mode: 'cors',
      headers: header
    })
    .then(response => response.json())
    .then(prices => this.setPrices(prices))
  }

  getIcons(prices){
    var header = new Headers({
      'Access-Control-Allow-Origin':'*',
      'Content-Type': 'multipart/form-data'
    });
    return fetch(config.api.urlIcons, {
      mode: 'cors',
      headers: header
    })
    .then(response => response.json())
    .then(icons => this.setIcons(icons.Data))
  }

  setIcons(icons){
    var newSet = this.state.datas.map((item) => {
      if (icons[item.symbol] !== undefined){
        item.icon = this.state.baseUrlImage + icons[item.symbol].ImageUrl;
        item.id = icons[item.symbol].Id;
      }
      return item;
    })

    this.setState({
      datas: newSet
    });
  }

  setPrices(prices){
    var total = 0;
    var newSet = this.state.datas.map((item) => {
      if (prices[item.symbol] !== undefined){
        item.price = prices[item.symbol].USD;

        item.total_unit = (item.confirmed + item.unconfirmed)
        item.value = item.price * item.total_unit;
        total += item.value;
      }
      return item;
    })

    this.setState({
      datas: newSet,
      total: total
    });

    console.log(this.state.datas);
  }

  getCurr(result, key) {
    if (result.getuserallbalances && result.getuserallbalances.data){
      let data = result.getuserallbalances.data;
      console.log(result.getuserallbalances.data);

      let curr = data.map((item) => {
        item.symbol = symbols[item.coin];
        return symbols[item.coin]
      }).join();

      this.setState({
        isLoggedIn: true,
        datas: data,
        curr: curr
      });

      sessionStorage.setItem('apiKey', key);
      return curr;
    }
  }

  handleLoginClick(event) {
  }

  handleLogoutClick(event) {
    sessionStorage.removeItem('apiKey');
    this.setState({
      isLoggedIn: false,
      apiKey: null,
      datas: null,
      curr: null,
    });
  }

  render() {
    const isLoggedIn = this.state.isLoggedIn;
    const actions = [
      <FlatButton
        label="Cancel"
        primary={true}
        onClick={this.handleClose}
      />
    ];


    let elems = null;
    if (isLoggedIn) {
      elems =
      <div>
        <div style={{
          float: 'right',
          padding: 20
        }}>
          <RaisedButton label="log out" onClick={this.handleLogoutClick} style={{ flex: 1 }}/>
        </div>
        <div style={{
          float: 'right',
          marginBottom: 20,
          padding: 20,
          textAlign: 'right'
        }}>
          <span>Last updated: {this.state.lastUpdate}</span><br/>
          <span>Now: {this.state.currentTime}</span><br/>
        </div>

        <div style={{
          float: 'left',
          padding: 20
        }}>
          <Crypto items={this.state.datas} total={this.state.total} style={{
            float: 'left',
            padding: 20
          }}/>
        </div>
      </div>
    } else {
      elems =
      <div>
        <Paper style={stylePaper} zDepth={1}>
          <form type="submit" onSubmit={this.onSubmit}>
            <TextField floatingLabelText="Enter Your ApiKey" ref={el => this.apiKey = el}  style={styleText}/>
            <br/>
            <RaisedButton label="LOG IN" type="submit" className="button-submit"/>
          </form>
        </Paper>
        <Dialog
          title="Nope"
          actions={actions}
          modal={false}
          open={this.state.openErrors}
          onRequestClose={this.handleClose}
        >
          API KEY DOES NOT WORK
        </Dialog>
      </div>
    }


    return (
      <div>
        <div className='App-header'>
          <h1>Mining Profits</h1>
        </div>

        {elems}

      </div>
    );
  }
}

export default App;



